/* Loop through the aspect-ratios map in variables.scss. Use both values defined in of each $aspect-ratio in the map.
Calculate correct percentage using both values to force aspect-ratio via padding. Use the $aspect-ratio key to make the correct class names available.
Set aspect-ratio class together with the aspect-ratio-parent class on the container element.*/
.aspect-ratio-1-1 {
  padding-bottom: 100%; }

