/*
==========================================================
 KADASTER TYPOGRAPHY
==========================================================
* Kadaster specific typography styling
*/
/*
-----------------------------------------------------------
 HEADINGS
-----------------------------------------------------------
* Font, line-height, and color for body text, headings, and more.
*/
h2, h3, h4, .h2, .h3, .h4 {
  margin-top: 2.5rem; }

/* H1 does not have a margin-top */
h1, .h1 {
  line-height: 4rem; }

h2, .h2 {
  line-height: 2.125rem; }

h3, .h3 {
  line-height: 1.875rem; }

h4, .h4 {
  line-height: 1.625rem; }

/* margin-bottom is set in bootstrap via $headings-margin-bottom; for all headings */
h1, .h1, h4, .h4 {
  margin-bottom: 0.375rem; }

/* h2 & h3 use this variable, therefore are not declared here */
h2, h3, .h2, .h3, .display {
  margin-top: 2.5rem; }

h4, .h4 {
  margin-top: 1.25rem; }

/* Set specific font color for h4 by design; this should not change for private or business. See $headings-color & headings-color-4 variable. */
h4, .h4 {
  color: #406a9e; }

/* used for subheading */
.display {
  font-size: 2.125rem;
  font-weight: 600;
  color: #406a9e;
  margin-bottom: 0.75rem;
  line-height: 2.75rem; }

/* used for links in heading elements */
.header-link {
  color: #406a9e; }
  .header-link:hover, .header-link:focus, .header-link:active {
    color: #00387d; }

.lead {
  line-height: 2.25rem;
  margin-bottom: 1.25rem; }

.lead-sm {
  font-size: 1.125rem;
  line-height: 1.875rem; }

.text-sm {
  font-size: 0.875rem;
  line-height: 1.375; }

.text-xs {
  font-size: 0.75rem;
  line-height: 1.25rem; }

/*
-----------------------------------------------------------
 LINKS
-----------------------------------------------------------
* Link types
*/
.social {
  text-decoration: none; }
  .social:hover {
    text-decoration: none; }

/*
-----------------------------------------------------------
 BLOCKQUOTES
-----------------------------------------------------------
*/
.blockquote {
  text-align: center; }

.blockquote-content {
  display: flex; }
  .blockquote-content:before, .blockquote-content:after {
    color: #406a9e;
    font-size: 1.7rem; }
  .blockquote-content:before {
    content: "\f10d";
    margin-right: 2rem; }
  .blockquote-content:after {
    content: "\f10e";
    margin-left: 2rem;
    align-self: flex-end; }

.blockquote-footer:before {
  content: ""; }

/*
-----------------------------------------------------------
 ICONS
-----------------------------------------------------------
* Icon specific styling */
.icon-prefix,
.icon-suffix {
  height: 1em; }

.icon-prefix {
  margin-right: 0.375rem; }

.icon-suffix {
  margin-left: 0.375rem;
  text-align: right; }

.icon-suffix-left-align {
  margin-left: -0.375rem; }

/*
-----------------------------------------------------------
 LISTS
-----------------------------------------------------------
* List styling
*/
ul:not([class]) {
  padding-left: 0;
  list-style-type: none;
  margin-left: 2.25rem; }
  ul:not([class]) > li {
    position: relative; }
  ul:not([class]) li:before {
    position: absolute;
    left: -2.25rem;
    color: #406a9e;
    content: "\2022"; }

ol:not([class]) {
  padding-left: 1rem; }
  ol:not([class]) li {
    padding-left: 0.5rem; }
    ol:not([class]) li::marker {
      font-weight: 600; }

/*
-----------------------------------------------------------
RESPONSIVE TYPOGRAPHY
-----------------------------------------------------------
*/
/* Normally we go mobile first. Deliberately chose desktop first approach here
 to only apply styling for mobile resolution where needed.*/
@media (max-width: 991.98px) {
  h1, .h1 {
    font-size: 2.25rem; }
  .display {
    font-size: 2rem; } }

